// Typography
//
//


//
// Text hierarchy
//
html {
  @include responsive-font-size(); // enable for rem
}


//
// Body Text
//
body {
  background: $body-bg;
  color: $text-color;
  font-family: $font-family;
  font-weight: $font-weight;
  line-height: $line-height;
  letter-spacing: $letter-spacing;
}

.p-lead {
  margin-bottom: .4em;
  font-size: $font-size-lead;
  @at-root .text + .p-lead {
    margin-top: .8em;
  }
}
%p-small {
  font-size: $font-size-small;
}
%p-smaller {
  font-size: $font-size-smaller;
}


//
// Headings
//
h1, h2, h3, h4, h5, h6 {
  font-weight: $heading-font-weight;
  line-height: 1;
  color: $heading-text-color;
}
.heading-1 {
  font-size: $heading-1;
}
.heading-2 {
  font-size: $heading-2;
}

// 
// Lists
//
//ol, ul {
