//
// Quotes
//
blockquote {
  position: relative;
  &:before,
  &:after {
    position: relative;
    display: inline-block;
    width: rem(20px);
    height: rem(15px);
    vertical-align: baseline;
    background-repeat: no-repeat;
    background-size: contain;
  }
  &:before {
    content: '';
    top: -.5em;
    margin-right: .3em;
    background-image: url('data:img/png;base64,');
  }
  &:after {
    content: '';
    top: -.3em;
    margin-left: .3em;
    background-image: url('data:img/png;base64,');
  }
}

//
// Inline elements
//
a {
  color: $link-color;
  text-decoration: none;
  @media (pointer: fine) {
    &:hover {
      color: $link-color-hover;
    }
  }
}

//
// Etc
//
%eng {
  font-family: $font-family-eng;
}
