// Fill Text
//
.fill-text {
  position: relative;
  display: inline-block;
  white-space: nowrap;
  .color {
    overflow: hidden;
    position: absolute;
    top: 0;
    left: 0;
    width: 0;
  }
}

video {
  width: 100%;
  background: #000; // 임시
}